<script>
let fs = require("flowfs");

import {onMount} from "svelte";
import powershell from "./modules/powershell/renderer";

let url = "";
let cmd = "";
let output = "";
let ps;

async function openIe() {
	await ps.run(`
		$ie = new-object -comObject InternetExplorer.Application
		$ie.Visible = $True
	`);
}

async function navigate() {
	await ps.run(`
		$ie.Navigate("${url}")
	`);
}

async function closeIe() {
	await ps.run(`
		$ie.Quit()
	`);
}

async function run() {
	try {
		output += "\n" + await ps.run(cmd);
	} catch (e) {
		output += e;
	}
	
	cmd = "";
}

onMount(async function() {
	ps = await powershell();
	console.log(ps);
});
</script>

<style type="text/scss">#main {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1em; }

#test {
  padding: 1em; }

#ps {
  padding: 1em; }

#output {
  font-family: monospace;
  white-space: pre; }
/*# sourceMappingURL=src/App.svelte.map */</style>

<div id="main">
	<h3>IE</h3>
	<div id="test">
		<form on:submit|preventDefault={navigate}>
			<label>
				<input bind:value={url}>
			</label>
			<input type="submit" value="Go">
		</form>
		<br><br>
		<button on:click={openIe}>Open</button>
		<button on:click={closeIe}>Close</button>
	</div>
	<h3>PS</h3>
	<div id="ps">
		<form on:submit|preventDefault={run}>
			<label>
				<input bind:value={cmd}>
			</label>
			<input type="submit" value="Run">
		</form>
		<br><br>
		<div id="output">
			{output}
		</div>
	</div>
</div>
